import React, { memo, useRef } from 'react';
import { ScrollView } from 'react-native';
import { Form, Input, FormInstance, Button } from 'rn-vant';
import { DemoBlock } from '../../components';

interface FormValue {
  username: string;
  password: string;
}

const FormExample = memo(() => {
  const formRef = useRef<FormInstance<FormValue>>(null);

  return (
    <ScrollView>
      <DemoBlock title="">
        <Form<FormValue> form={formRef}>
          <Form.Item label="" name="username" rules={{ required: '' }}>
            <Input placeholder="" />
          </Form.Item>
          <Form.Item label="" name="password" rules={{ required: '' }}>
            <Input placeholder="" type="password" />
          </Form.Item>
        </Form>
        <Button round type="primary" style={{ marginHorizontal: 16, marginTop: 16 }}>
          
        </Button>
      </DemoBlock>
    </ScrollView>
  );
});

export default FormExample;
